.header {
  background: #0e2a47;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.nav-bar__links {
  display: flex;
  gap: 10px;
  align-items: center;
  justify-content: flex-end;
}
.nav-bar__btn {
  background: none;
  border: none;
  color: #ffebcd;
  transition: 0.4s;
  text-decoration: none;
  &:hover {
    color: #ff0000;
  }
}
.nav-bar__btn-active {
  color: #0eff01;
}
.mobile-btn {
  position: relative;
  width: 25px;
  height: 20px;
  background: none;
  border: none;
  display: none;
  &::before {
    content: "";
    position: absolute;
    height: 2px;
    width: 100%;
    top: 0;
    left: 0;
    background: #ffebcd;
    transition: 0.4s;
  }
  &::after {
    content: "";
    position: absolute;
    height: 2px;
    width: 100%;
    bottom: 0;
    left: 0;
    background: #ffebcd;
    transition: 0.4s;
  }
  span {
    position: absolute;
    height: 2px;
    width: 100%;
    top: 45%;
    left: 0;
    background: #ffebcd;
  }
}

@media (max-width: 470px) {
  .mobile-btn {
    display: block;
    &.nav-active {
      &::before {
        content: "";
        position: absolute;
        top: 45%;
        left: 0;
        transform: rotate(45deg);
      }
      &::after {
        content: "";
        position: absolute;
        bottom: 45%;
        left: 0;
        transform: rotate(-45deg);
      }
      span {
        display: none;
      }
    }
  }
  .nav-bar__links {
    display: none;
    animation: menuopener 0.5s;
    &.nav-active {
      display: flex;
      position: fixed;
      top: 60px;
      left: 0;
      width: 100%;
      height: 100%;
      justify-content: center;
      background: #000000ec;
      z-index: 10000;
    }
  }
}
@keyframes menuopener {
  0% {
    transform: translateY(-50%) scaleY(0);
  }
  100% {
    transform: translateY(0) scaleY(1);
  }
}
